// Simulate API delay
const delay = (ms: number) => new Promise(resolve => setTimeout(resolve, ms))

export async function fetchProducts(): Promise<Product[]> {
  try {
    await delay(500) // Simulate network delay
    return mockProducts
  } catch (error) {
    console.error('Error fetching products:', error)
    throw new Error('Falha ao carregar produtos')
  }
}

export async function fetchProduct(slug: string): Promise<Product | null> {
  try {
    await delay(300) // Simulate network delay
    const product = mockProducts.find(p => p.slug === slug)
    return product || null
  } catch (error) {
    console.error('Error fetching product:', error)
    throw new Error('Falha ao carregar produto')
  }
}

export async function fetchFeaturedProducts(): Promise<Product[]> {
  try {
    await delay(400) // Simulate network delay
    // Return first 4 products as featured
    return mockProducts.slice(0, 4)
  } catch (error) {
    console.error('Error fetching featured products:', error)
    throw new Error('Falha ao carregar produtos em destaque')
  }
}

export interface CheckoutData {
  customerInfo: {
    name: string
    email: string
    phone: string
  }
  shippingAddress: {
    street: string
    number: string
    complement?: string
    neighborhood: string
    city: string
    state: string
    zipCode: string
  }
  paymentInfo: {
    method: 'credit_card' | 'pix' | 'boleto'
    cardNumber?: string
    cardName?: string
    cardExpiry?: string
    cardCvv?: string
  }
  items: {
    productId: string
    quantity: number
    price: number
  }[]
  total: number
}

export async function processCheckout(data: CheckoutData): Promise<{ success: boolean; orderId?: string; error?: string }> {
  try {
    await delay(2000) // Simulate payment processing
    
    // Basic validation
    if (!data.customerInfo.name || !data.customerInfo.email) {
      throw new Error('Informa√ß√µes do cliente incompletas')
    }
    
    if (!data.items.length) {
      throw new Error('Carrinho vazio')
    }
    
    // Simulate payment processing
    const success = Math.random() > 0.1 // 90% success rate
    
    if (success) {
      const orderId = `LJ${Date.now()}`
      return { success: true, orderId }
    } else {
      throw new Error('Falha no processamento do pagamento')
    }
  } catch (error) {
    console.error('Error processing checkout:', error)
    return { 
      success: false, 
      error: error instanceof Error ? error.message : 'Erro desconhecido' 
    }
  }
}
